/* üìÑ ÌÖåÏù¥Î∏î Î∞è ÏÉÅÎã® Ìó§Îçî */
.knowledge-table-wrapper {
  background-color: #fff;
  padding: 24px;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.table-controls {
  display: flex;
  gap: 12px;
  align-items: center;
}

.table-controls select,
.table-controls input {
  padding: 6px 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.btn-new {
  background-color: #2563eb;
  color: #fff;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  cursor: pointer;
}

.btn-new:hover {
  background-color: #1d4ed8;
}

.knowledge-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.knowledge-table th,
.knowledge-table td {
  padding: 12px;
  text-align: left;
  font-size: 14px;
  border-bottom: 1px solid #e5e7eb;
}

.btn-edit,
.btn-delete {
  border: none;
  background: none;
  font-size: 16px;
  cursor: pointer;
  margin-right: 6px;
}

.btn-delete {
  color: red;
}

.pagination {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  gap: 6px;
}

.pagination button {
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
}

.pagination .active {
  background-color: #2563eb;
  color: white;
  font-weight: bold;
}

/* üí¨ Î™®Îã¨ Î∞∞Í≤Ω */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

/* üí¨ Î™®Îã¨ Ïª®ÌÖåÏù¥ÎÑà */
.modal {
  background-color: white;
  padding: 24px;
  border-radius: 8px;
  width: 100%;
  max-width: 640px;         /* ‚úÖ 600~640px Ï∂îÏ≤ú */
  display: flex;
  flex-direction: column;
  gap: 14px;
  overflow-x: hidden;       /* ‚úÖ Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Ï†úÍ±∞ */
  box-sizing: border-box;   /* ‚úÖ Ìå®Îî©/Î≥¥Îçî Ìè¨Ìï®Ìïú ÌÅ¨Í∏∞ Í≥ÑÏÇ∞ */
}


.modal h3 {
  margin: 0 0 10px;
  font-size: 20px;
  font-weight: 600;
  color: #111827;
}

/* ‚úÖ Î™®Îã¨ ÎùºÎ≤®-ÏûÖÎ†• 2Ïó¥ Ï†ïÎ†¨ Ïä§ÌÉÄÏùº */
.modal-row {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.modal-row label {
  width: 120px;
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  padding-top: 8px;
}

.modal-row .input-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.modal-row .input-area input,
.modal-row .input-area textarea,
.modal-row .input-area select {
  padding: 10px 12px;
  font-size: 14px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background-color: #f9fafb;
  transition: border-color 0.2s;
}

.modal-row .input-area textarea {
  resize: vertical;
  min-height: 80px;
}

.modal-row .input-area input:focus,
.modal-row .input-area textarea:focus,
.modal-row .input-area select:focus {
  outline: none;
  border-color: #2563eb;
  background-color: #fff;
}

/* Ï≤®Î∂ÄÌååÏùº ÌûåÌä∏ */
.file-preview {
  font-size: 13px;
  color: #6b7280;
}

/* üí¨ Î™®Îã¨ ÌïòÎã® Î≤ÑÌäº Ï†ïÎ†¨ */
.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 6px;
}

.modal-actions button {
  padding: 8px 14px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}

.modal-actions button:hover {
  opacity: 0.95;
}

.modal-actions .danger {
  background-color: #ef4444;
  color: white;
}

.modal-actions .cancel {
  background-color: #f3f4f6;
  color: #374151;
}

.modal-actions .primary {
  background-color: #2563eb;
  color: white;
}

/* ‚ú® ÌéòÏù¥Îìú Ïù∏ Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
