/* MyEmployeePage.css */

/* Ïπ¥Îìú Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */
.inquiry-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.08);
  margin-bottom: 1rem;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* ÌôïÏû•Îêú Ïπ¥Îìú Í∞ïÏ°∞ */
.inquiry-card.expanded {
  border-left: 4px solid #007bff;
  background-color: #fdfdfd;
}

/* Ïπ¥Îìú Ìó§Îçî (Ïπ¥ÌÖåÍ≥†Î¶¨, Ï†úÎ™©, ÎÇ†Ïßú, ÏÇ≠Ï†ú) */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-header .left-group {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.card-header .right-group {
  display: flex;
  align-items: center;
  gap: 0.7rem;
}

.card-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
}

/* ÌÉúÍ∑∏ Ïä§ÌÉÄÏùº */
.category-tag {
  display: inline-block;
  padding: 3px 8px;
  border-radius: 5px;
  background-color: #eee;
  font-size: 0.8rem;
  color: #333;
}

.answer-status {
  display: inline-block;
  padding: 3px 8px;
  border-radius: 5px;
  font-size: 0.8rem;
  font-weight: 500;
  margin-left: 0.5rem;
}

.answer-status.answered {
  background-color: #d1e7dd;
  color: #0f5132;
}

.answer-status.pending {
  background-color: #f8d7da;
  color: #842029;
}

/* Ïπ¥Îìú ÎÇ¥Ïö© */
.card-details {
  margin-top: 1rem;
  font-size: 0.95rem;
  line-height: 1.5;
  padding-top: 1rem;
  border-top: 1px solid #eee;
  color: #444;
}

/* ÎãµÎ≥Ä/ÎåÄÍ∏∞ Î¨∏Íµ¨ */
.answer-section {
  margin-top: 1rem;
  background: #f1f8ff;
  padding: 0.8rem;
  border-radius: 6px;
}

.pending-answer-notice {
  margin-top: 1rem;
  padding: 0.8rem;
  background: #fff3cd;
  border-radius: 6px;
  color: #664d03;
}

/* ÏÇ≠Ï†ú Î≤ÑÌäº */
.btn-delete {
  background: none;
  border: none;
  font-size: 1.1rem;
  cursor: pointer;
  color: #999;
  transition: color 0.2s ease;
}

.btn-delete:hover {
  color: #e74c3c;
}

/* ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò */
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  gap: 0.5rem;
}

.pagination button {
  padding: 6px 12px;
  background-color: #ececec;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 500;
}

.pagination button.active {
  background-color: #1d4ed8;
  color: white;
}

.pagination button:disabled {
  opacity: 0.4;
  cursor: default;
}

/* Î™®Îã¨ Í≥µÌÜµ */
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(33, 33, 33, 0.4);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal.confirm {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  min-width: 320px;
  max-width: 500px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
}

.modal-footer {
  margin-top: 1.2rem;
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.modal-footer button {
  padding: 6px 14px;
  border: none;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
}

.modal-footer button:first-child {
  background-color: #ccc;
  color: #333;
}

.modal-footer button:last-child {
  background-color: #dc3545;
  color: white;
}

/* üõ† Í∞ïÏ†ú ÎçÆÏñ¥Ïì∞Í∏∞: Ï†úÎ™©Ïù¥ Î¨¥Ï°∞Í±¥ Î≥¥Ïù¥Í≤å */
.card-title {
  display: block !important;
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin: 0.3rem 0;
  white-space: normal;
}

/* üõ† ÌôïÏû•Îêú Ïπ¥ÎìúÏùò ÎÇ¥Ïö©Ïù¥ Î≥¥Ïù¥ÎèÑÎ°ù ÏÑ§Ï†ï */
.card-details {
  display: block !important;
  padding-top: 1rem;
  border-top: 1px solid #eee;
  color: #444;
}

/* üõ† Ïπ¥ÎìúÏóê ÏµúÏÜå ÎÜíÏù¥ ÌôïÎ≥¥ */
.inquiry-card {
  min-height: 80px;
  padding: 1rem;
}
